@charset "UTF-8";

// NHA-COMMON-FAQ-046
.faq_list_type01 {
  .faq_list_wrap {
    padding:40px 45px 0 0; border:1px solid transparent; border-bottom-color:$gray2;
    @include boxSizing;
    @include transition(border, .2s);
    &.active {border-color:$gray7;}
    &:last-of-type {
      border-bottom-color:transparent;
      &.active {border-bottom-color:$gray7;}
    }
    //&:hover, &:focus {border-color:$gray7;}
  }
  .row_list {
    display:flex;
    .tit_txt {font-size:14px; color:$gray5; font-weight:$medium;}
    .question_txt, .desc{padding:12px 0 45px;  font-size:18px; color:$gray7; font-weight:$regular; line-height:26px;}
    .question_txt{width:800px; position:relative;}
    .num_m { display:none; }
    .snb_s {
      width:100%; padding:0 0 0 20px;
      a {
        display:block; width:100%; position:relative;
        &.on {
          .toggle_icon {
            @include transform(rotate(180deg));
          }
        }
      }
    }
    .toggle_icon {
      width:13px; height:8px;display:block; background-size:cover; position:absolute;
      right:0; top:45%; margin:-10px 0 0; background:url(./../../images/common/select_arr_02.png) 50% 50% no-repeat;
      @include transition(all, .3s);
    }
    .num_txt {width:100%;max-width:95px; text-align:center;}
    .hidden_elm {
      p {
        position:relative; padding:40px 0;font-size:18px;font-weight:$regular;color:$gray7; line-height:26px;
        &:after {width:100%; height:1px;content:"";display:block;position:absolute; top:0;background-color:$gray2;}
      }
    }
    .answer_txt {
      padding:16px 0 30px;font-size:18px;color:$gray7;font-weight:$regular; display:flex; align-items:start;
      em {
        min-width:28px; min-height:28px;display:block;font-size:14px;font-weight:$extra-bold;line-height:25px;
        text-align:center;color:$blue5;background-color:#dcebfe; margin:0 10px 0 0;
        @include borderRadius(50px);
      }
    }
  }
  @include mob() {
    padding:em(30) 0;
    .faq_list_wrap {
      padding:0; border-color:transparent; border-bottom-color:$gray2; border:none;
      &:first-of-type {
        margin:0;
        .row_list {
          .tit_txt {display:flex;}
          .snb_s {
            .toggle_icon {margin:2rem 0 0;}
          }
        }
      }
      &:last-of-type {border-bottom-color:transparent;}
    }
    .row_list {
      .tit_txt {
        margin:0 em(30); padding:em(33) em(35) em(27); font-size:em(28);color:$gray5;font-weight:$bold;
        display:none; background-color:#f5f5f5; line-height:em(30);
        @include borderRadius(em(5));
        &:before {content:"No.";display:inline-block;min-width:em(80);}
      }
      .question_txt, .desc {font-size:em(30);line-height:em(40);font-weight:$bold;}
      .question_txt {
        width:auto; padding:em(40) em(100) em(40) em(35); display:flex; margin:0 em(30);
        .num_m {min-width:em(80); display:flex; align-items:center;}
        span {display:block;}
      }
      .snb_s {
        padding:0;
        a {
          position:relative;
          &.on {
            .toggle_icon {
              @include transform(translate(0,-50%) rotate(0deg));
            }
          }
        }
        .toggle_icon {
          width:em(24); height:em(15);position:absolute;left:auto;right:em(40);top:50%; margin:0;
          background:url(./../../images/common/models_arr.png) 50% 50% no-repeat;background-size:cover;
          @include transform(translate(0,-50%) rotate(180deg));
        }
      }
      .num_txt {display:none;}
      .hidden_elm {
        p {
          padding:em(50) em(30) em(40) em(45);line-height:em(40);font-size:em(30);
          &:after {display:none;}
        }
      }
      .answer_txt {
        padding:0;color:$gray7;font-weight:$regular;font-size:em(30); background-color:$gray1;
        em {
          text-align:center; min-width:em(60);min-height:em(60);margin:em(-10) em(25) 0 0;
          font-size:em(28); padding:em(5) 0; font-weight:$bold;display:flex;
          align-items:center;justify-content:center;
          @include boxSizing;
          @include borderRadius(em(50));
        }
      }
    }
  }
}

//NHA-COMMON-PRODUCT DOCUMENTATION-034
.product_documentation {
  .content {max-width:1200px; margin:0 auto; padding:40px 0;}
  .series_list {
    margin:80px 0 0; font-size:16px;
    &:nth-of-type(1) {margin:0;}
    p {
      color:$gray5; background-color:$gray1; line-height:44px; padding:0 0 0 30px;
      @include borderRadius(5px)
    }
    ol {
      padding:20px 0 0;
      @extend %clearfix;
    }
    li {
      width:220px; padding:30px 0 0 30px; float:left;
      @include boxSizing;
    }
    a {
      color:$gray7; border-bottom:2px solid #a6a6a6; display:inline-block; font-weight:$medium;
      @include transition;
      &:hover,
      &:focus {color:$blue; border-bottom-color:$blue;}
    }
  }

  @include mob() {
    padding:em(60) em(30);
    .series_list {
      margin:em(10) 0 0; font-size:em(32);
      p {
        line-height:em(88); padding:0 0 0 em(35); font-weight:$bold;
        @include borderRadius(0.5rem)
      }
      ol {padding:em(60) em(35) 0; display:flex; flex-wrap:wrap;}
      li {width:50%; padding:0 0 em(50);}
      a {font-weight:$bold;}
    }
  }
}

// NHA-COMMON-SW_DOWNLOAD_DETAIL-049
.download_detail_type {
  .substance_wrap {
    background-color:$white;
    .substance_cont {
      width:100%;max-width:1200px;margin:0 auto; padding:20px 0 80px;
      .tab_wrap {
        width:100%; border-bottom:1px solid $gray2;
        @include boxSizing;
        ul {
          width:fit-content; margin:0 auto;
          @extend %clearfix;
        }
        li {margin:0 20px;float:left;}
        button {
          display:block;padding:15px 40px;font-size:18px;color:$gray5;font-weight:$regular; position:relative;
          @include transition(all, .2s);
          &:after {
            width:100%; height:2px; background-color:$blue5;content:"";display:block;
            position:absolute; bottom:-1px;left:0; opacity:0;
            @include transition(all, .2s);
          }
          &.active {
            font-weight:$medium; color:$gray7;
            &:after {opacity:1;}
          }
          &:hover, &:focus {
            color:$gray7;
            &:after {opacity:1;}
          }
        }
      }
      .definition_row {
        width:100%; padding:50px 0 0; position:relative;
        .download_list {
          float:right;position:absolute;right:50px;top:50%;
          @include transform(translate(0, -50%));
          a {
            display:block;padding:13px 33px 13px 20px; font-size:13px;
            font-weight:$medium;color:$white; text-align:center; border:1px solid $gray7;
            background:url(./../../images/common/download_brochure_icon_w.png) 84% 50% no-repeat $gray7;
            background-size:10px;
            @include transition(all, .2s);
            @include boxSizing;
            @include borderRadius(100px);
            &:hover, &:focus {
              border-color:$gray3;color:$gray7;
              background:url(./../../images/common/download_brochure_icon.png) 84% 50% no-repeat $white;
            }
          }
        }
        // 전체 테이블
        ol {
          display:none;
          @extend %clearfix;
          &:first-of-type { display:block; }
          > li {
            width:100%; border:1px solid $gray2; padding:30px 50px; margin:20px 0 0; position:relative;
            @extend %clearfix;
            @include boxSizing;
            @include borderRadius(20px);
            &:first-of-type { margin:0; }
          }
        }
        .table_list {width:940px;float:left;display:flex; justify-content:space-between;text-align:center;}
        dl {
          @extend %clearfix;
          &:first-of-type {
            > dd { padding:0; }
          }
          &:nth-of-type(1) {width:330px;}
          &:nth-of-type(2) {
            width:450px;
            dd {width:300px;margin:0 auto;}
          }
          &:nth-of-type(3) {width:160px;}
          > dt {border-bottom:1px solid $gray2;padding:0 20px 16px;font-size:16px;font-weight:$medium;color:$gray7;line-height:24px;}
          > dd {padding:20px;font-size:14px;color:$gray5;font-weight:$regular;line-height:22px;}
        }
        // 옵션 테이블
        ul {
          @extend %clearfix;
          > li {
            width:33.333%; float:left;
            span {
              display:block; padding:10px; font-size:14px;color:$gray5;font-weight:$regular;line-height:22px;
              &:first-of-type { border-bottom:1px solid $gray2;font-weight:$bold;font-size:13px;}
            }
          }
        }
      }
    }
  }
  @include mob() {
    .substance_wrap {
      .substance_cont {
        max-width:inherit;padding:0 0 em(100);
        .tab_wrap {
          border-bottom:em(2) solid $gray2;
          ul {width:auto; padding:0 em(30);}
          li {
            float:left; margin:0 em(25);
            &:first-of-type { margin:0 em(25) 0 0; }
          }
          button {
            padding:em(30) 0;font-size:em(32);font-weight:$bold;
            &:after {width:100%; height:em(4);bottom:em(-2); opacity:0;}
            &.active {
              font-weight:$bold;
              &:after {opacity:1;}
            }
          }
        }
        .definition_row {
          padding:em(40) em(30) 0;
          @include boxSizing;
          .download_list {
            width:100%; position:static;
            @include transform(translate(0, 0));
            a {
              width:100%; padding:em(25) 0; font-size:em(26);font-weight:$bold;
              background:url(./../../images/common/download_brochure_icon_w.png) 65% 50% no-repeat $gray7;
              background-size:em(15);
              @include borderRadius(0);
              &:hover, &:focus {
                border-color:$gray7;color:$white;
                background:url(./../../images/common/download_brochure_icon_w.png) 65% 50% no-repeat $gray7;
              }
            }
          }
          // 전체 테이블
          ol {
            > li {
              border:em(2) solid $gray2; padding:0; margin:em(25) 0 0; overflow:hidden;
              @include borderRadius(em(20));
            }
          }
          .table_list {
            width:100%;float:inherit;display:block;padding:em(15) em(50);
            @include boxSizing;
          }
          dl {
            padding:em(25) 0;
            &:nth-of-type(1) {width:auto;}
            &:nth-of-type(2) {
              width:auto;
              dd {width:60%;}
            }
            &:nth-of-type(3) {width:auto;}
            > dt {
              width:40%; float:left; border-bottom:none;padding:0;
              font-size:em(26);font-weight:$bold; line-height:em(32); text-align:left;
            }
            > dd {
              width:60%; float:left; padding:0;font-size:em(26);
              color:$gray5;font-weight:$regular;line-height:em(32); text-align:left;
            }
          }
          // 옵션 테이블
          ul {
            background-color:#f9f9f9; text-align:center;display:flex;justify-content:center;
            padding:em(13) 0;
            @include borderRadius(em(20));
            > li {
              width:auto; float:inherit;
              span {
                display:block; padding:em(20) em(15); font-size:em(24);line-height:em(26);
                &:first-of-type { border-bottom:1px solid $gray2;font-size:em(24);}
              }
            }
          }
        }
      }
    }
  }
}

// NHA-COMMON-PRODUCT_DOCUMENTATION_DETAIL-035
.download_detail_type.documentation_type {
  .substance_wrap {
    .substance_cont {
      .documentation_row {
        width:100%; padding:40px 0 0;
        @extend %clearfix;
        ol {
          display:none;
          @extend %clearfix;
          &:first-of-type { display:block; }
        }
        li {
          width:560px; padding:8px 0; border-bottom:1px solid $gray1; float:left;
          &:nth-of-type(2n+2) { margin:0 0 0 80px;}
          span {font-size:14px;font-weight:$regular;color:$gray7; float:left; padding:20px 0 0;}
          a {
            width:fit-content;float:right;padding:4px 35px 4px 20px;display:block;font-size:13px;
            color:$gray7;font-weight:$medium; line-height:32px; border:1px solid $gray3;
            @include transition(border, .2s);
            @include boxSizing;
            @include borderRadius(100px);
            background:url(./../../images/common/download_brochure_icon.png) 80% 50% no-repeat; background-size:10px;
            &:hover, &:focus {border-color:$gray7;}
          }
        }
      }
    }
  }
  @include mob() {
    .substance_wrap {
      .substance_cont {
        .documentation_row {
          padding:em(20) em(30) 0;
          @include boxSizing;
          ul {width:100%;}
          li {
            width:100%; padding:em(25) 0; border-bottom:em(2) solid $gray1;
            &:nth-of-type(2n+2) { margin:0;}
            span {font-size:em(26);font-weight:$bold; padding:em(30) 0 0;}
            a {
              padding:em(20) em(50) em(15) em(30);font-size:em(25);
              color:$gray7;font-weight:$bold; line-height:em(30); border:em(2) solid $gray3;
              background:url(./../../images/common/download_brochure_icon.png) 85% 50% no-repeat;
              background-size:em(15);
            }
          }
        }
      }
    }
  }
}